// app/utils/products.js

import { readFile, writeFile, mkdir } from 'fs/promises';
import { join } from 'path';
import axios from 'axios';
import { getVendusApi } from "../services/settings.server";

// Data directory and file paths
const DATA_DIR = join(process.cwd(), "app", "data");
const SEARCH_RESULTS_FILE = join(DATA_DIR, "search_results.json");
const SHORTCUTS_FILE = join(DATA_DIR, "shortcuts.json");

// Replace with your actual Shopify Location ID
// This ID is crucial for managing inventory at a specific location in Shopify.
// Ensure this ID is correct for your Shopify store. You can fetch it via the Shopify GraphQL API.
const SHOPIFY_LOCATION_ID = "gid://shopify/Location/102699630920"; // EXAMPLE ID, REPLACE WITH YOURS!

// Vendus API Configuration (from your provided script)
const VENDUS_URL = "https://www.vendus.pt/ws/v1.1/products";

// Retry configuration for Vendus API calls
const RETRY_CONFIG = {
  maxRetries: 3,
  baseDelay: 1000,
  maxDelay: 8000,
  backoffFactor: 2
};

// Sleep function for delays
const sleep = (ms) => new Promise(resolve => setTimeout(resolve, ms));

// Calculate exponential backoff delay
function getRetryDelay(attempt) {
  const delay = RETRY_CONFIG.baseDelay * (RETRY_CONFIG.backoffFactor ** (attempt - 1));
  return Math.min(delay, RETRY_CONFIG.maxDelay);
}

// Function to safely fetch data from Vendus API with retries
async function fetchVendusDataWithRetries(url, apiToken, attempt = 1) {
  try {
    const response = await axios.get(url, {
      headers: {
        'Accept': 'application/json',
        'Authorization': `Bearer ${apiToken}`
      },
      timeout: 10000 // 10 seconds timeout for Vendus API
    });
    return response.data;
  } catch (error) {
    console.error(`Vendus API call failed (Attempt ${attempt}):`, error.message);
    if (attempt < RETRY_CONFIG.maxRetries) {
      const delay = getRetryDelay(attempt);
      console.log(`Retrying in ${delay}ms...`);
      await sleep(delay);
      return fetchVendusDataWithRetries(url, apiToken, attempt + 1);
    } else {
      throw new Error(`Failed to fetch from Vendus API after ${RETRY_CONFIG.maxRetries} attempts: ${error.message}`);
    }
  }
}

// Function to run the product search and update search_results.json
export async function runSearch(productReferencesToRefresh) {
  try {
    // Ensure data directory exists
    await mkdir(DATA_DIR, { recursive: true });

    // 1. Load existing search_results.json or initialize if it doesn't exist
    let jsonData = {
      searchDate: new Date().toISOString(),
      totalSearched: 0,
      totalFound: 0,
      found: {},
      notFound: []
    };

    try {
      const fileContent = await readFile(SEARCH_RESULTS_FILE, 'utf8');
      jsonData = JSON.parse(fileContent);
    } catch (readError) {
      if (readError.code === 'ENOENT') {
        console.log("search_results.json not found, creating new one.");
      } else {
        console.error("Failed to read search_results.json:", readError);
        return { success: false, error: `Failed to load search results file: ${readError.message}` };
      }
    }

    // Prepare a set of all product references to search/refresh
    const allReferencesToSearch = new Set(productReferencesToRefresh);
    if (jsonData.found) {
      Object.keys(jsonData.found).forEach(ref => allReferencesToSearch.add(ref));
    }

    jsonData.totalSearched = allReferencesToSearch.size;

    // Fetch Vendus API token
    const vendusApi = await getVendusApi();
    const vendusApiToken = vendusApi.token;

    const allVendusProducts = [];
    const foundReferences = new Set();

    // 2. Fetch product data from Vendus for each reference
    for (const ref of allReferencesToSearch) {
      try {
        console.log(`üîç Searching for product with reference: ${ref}`);
        const vendusProductData = await fetchVendusDataWithRetries(`${VENDUS_URL}?ref=${encodeURIComponent(ref)}`, vendusApiToken);

        if (vendusProductData && vendusProductData.data && vendusProductData.data.length > 0) {
          // Assuming the first result is the most relevant
          const product = vendusProductData.data[0];
          allVendusProducts.push({
            reference: ref, // Store the reference used for search
            ...product
          });
          console.log(`   ‚úÖ Found product: ${product.title} (${product.reference})`);
        } else {
          console.log(`   ‚ùå Product with reference '${ref}' not found in Vendus.`);
        }
      } catch (vendusError) {
        console.error(`‚ùå Error fetching product '${ref}' from Vendus:`, vendusError.message);
        jsonData.notFound.push(ref);
      }
    }

    // 3. Update jsonData.found based on fetched products
    jsonData.found = {}; // Clear previous found data to refresh
    for (const ref of productReferencesToRefresh) {
      const foundProduct = allVendusProducts.find(p => p.reference === ref);
      if (foundProduct) {
        jsonData.found[ref] = {
          productData: foundProduct,
          page: foundProduct.page,
          position: allVendusProducts.indexOf(foundProduct) + 1 // Simple position
        };
        foundReferences.add(ref);
      } else {
        jsonData.notFound.push(ref);
      }
    }

    jsonData.totalFound = foundReferences.size;
    jsonData.searchDate = new Date().toISOString();

    // 4. Save the updated JSON file
    try {
      await writeFile(SEARCH_RESULTS_FILE, JSON.stringify(jsonData, null, 2), 'utf8');
      console.log("‚úÖ Updated search_results.json with refreshed Vendus data.");
    } catch (writeError) {
      console.error("‚ùå Failed to write updated search_results.json:", writeError);
      return {
        success: false,
        error: `Failed to save updated search results file: ${writeError.message}`,
        filePath: SEARCH_RESULTS_FILE
      };
    }

    return {
      success: true,
      searchResults: jsonData,
      message: `Search completed. Found ${jsonData.totalFound} of ${jsonData.totalSearched} references.`
    };

  } catch (error) {
    console.error("Top-level error in runSearch:", error);
    return {
      success: false,
      error: `An unexpected error occurred during search: ${error.message}`
    };
  }
}
